













import { Vue, Component } from 'vue-property-decorator';

import { WebPagePath } from '@/config';

// vue-sidebar-menu
import VueSidebarMenu from 'vue-sidebar-menu';
import 'vue-sidebar-menu/dist/vue-sidebar-menu.css';
Vue.use(VueSidebarMenu);

//#region interface
interface INavListBase {
    title: string;
    href?: string;
    icon?: string;
    hiddenOnCollapse?: boolean;
}

interface INavList extends INavListBase {
    child?: INavListBase[];
}

interface INavItem {
    collapsed: boolean;
    showOneChild: boolean;
}
//#endregion interface

@Component({
    components: {},
})
export default class NavSrc extends Vue {
    //#region variables
    private navList: INavList[] = [];

    private navItem: INavItem = {
        collapsed: false, // sidebar state. true: collapsed. false: not collapsed
        showOneChild: true, // Keep only one child opened at a time (first level only)
    };
    //#endregion variables

    //#region vue life
    private beforeCreate(): void {}
    private created(): void {
        this.initNavList();
    }
    //#endregion vue life

    //#region init nav
    private initNavList(): void {
        this.navList = [
            // Login
            {
                title: this.$i18n.Navigation_Login,
                href: WebPagePath.Login,
                icon: 'fa fa-address-card-o',
            },

            // Component
            {
                title: this.$i18n.Navigation_Component,
                icon: 'fa fa-database',
                child: [
                    {
                        href: WebPagePath.FormsDemo,
                        title: this.$i18n.Navigation_Component_Forms,
                    },
                    {
                        href: WebPagePath.FormRelationalSelectDemo,
                        title: this.$i18n.Navigation_Component_FormRelationalSelect,
                    },
                    {
                        href: WebPagePath.ImageDemo,
                        title: this.$i18n.Navigation_Component_Image,
                    },
                    {
                        href: WebPagePath.ToastDemo,
                        title: this.$i18n.Navigation_Component_Toast,
                    },
                    {
                        href: WebPagePath.CardDemo,
                        title: this.$i18n.Navigation_Component_Card,
                    },
                    {
                        href: WebPagePath.DialogDemo,
                        title: this.$i18n.Navigation_Component_Dialog,
                    },
                    {
                        href: WebPagePath.ModalDemo,
                        title: this.$i18n.Navigation_Component_Modal,
                    },
                    {
                        href: WebPagePath.LoadingDemo,
                        title: this.$i18n.Navigation_Component_Loading,
                    },
                    {
                        href: WebPagePath.Progress,
                        title: this.$i18n.Navigation_Component_Progress,
                    },
                    {
                        href: WebPagePath.Tree,
                        title: this.$i18n.Navigation_Component_Tree,
                    },
                    {
                        href: WebPagePath.Search,
                        title: this.$i18n.Navigation_Component_Search,
                    },
                    {
                        href: WebPagePath.TableDemo,
                        title: this.$i18n.Navigation_Component_Table,
                    },
                    {
                        href: WebPagePath.StepDemo,
                        title: this.$i18n.Navigation_Component_Step,
                    },
                    {
                        href: WebPagePath.Toolbox,
                        title: this.$i18n.Navigation_Component_Toolbox,
                    },
                    {
                        href: WebPagePath.ROI,
                        title: this.$i18n.Navigation_Component_ROI,
                    },
                ],
            },

            // Form Type
            {
                title: this.$i18n.Navigation_Form_Type,
                icon: 'fa fa-battery-empty',
                child: [
                    {
                        href: WebPagePath.FormEdit,
                        title: this.$i18n.Navigation_Form_Type_Form_Edit,
                    },
                    {
                        href: WebPagePath.FormView,
                        title: this.$i18n.Navigation_Form_Type_Form_View,
                    },
                    {
                        href: WebPagePath.ScheduleDemo,
                        title: this.$i18n.Navigation_Form_Type_Schedule,
                    },
                    {
                        href: WebPagePath.SMTPDemo,
                        title: this.$i18n.Navigation_Form_Type_Settings_SMTP,
                    },
                ],
            },

            // Emap
            {
                title: this.$i18n.Navigation_Emap,
                href: WebPagePath.Emap,
                icon: 'fa fa-map-o',
            },

            // Table Type
            {
                title: this.$i18n.Navigation_Table_Type,
                icon: 'fa fa-table',
                child: [
                    // table base
                    {
                        href: WebPagePath.TableBase,
                        title: this.$i18n.Navigation_Table_Type_Table_Base,
                    },

                    // Table + Form ( Edit / View )
                    {
                        href: WebPagePath.TableFormView,
                        title: this.$i18n.Navigation_Table_Type_Table_Form_View,
                    },
                    {
                        href: WebPagePath.TableFormEdit,
                        title: this.$i18n.Navigation_Table_Type_Table_Form_Edit,
                    },
                    {
                        href: WebPagePath.TableFormEditView,
                        title: this.$i18n.Navigation_Table_Type_Table_Form_Edit_View,
                    },

                    // Table Card Search + Form ( Edit / View )
                    {
                        href: WebPagePath.TableInCardSearch,
                        title: this.$i18n.Navigation_Table_Type_Table_In_Card_Search,
                    },
                    {
                        href: WebPagePath.TableInCardSearchFormEdit,
                        title: this.$i18n.Navigation_Table_Type_Table_Table_In_Card_Search_Form_Edit,
                    },
                    {
                        href: WebPagePath.TableInCardSearchFormView,
                        title: this.$i18n.Navigation_Table_Type_Table_In_Card_Search_Form_View,
                    },

                    // Table Filter + Form ( Edit / View )
                    {
                        href: WebPagePath.TableFilter,
                        title: this.$i18n.Navigation_Table_Type_Table_Table_Filter,
                    },
                    {
                        href: WebPagePath.TableFilterFormView,
                        title: this.$i18n.Navigation_Table_Type_Table_Filter_Form_View,
                    },
                    {
                        href: WebPagePath.TableFilterFormEdit,
                        title: this.$i18n.Navigation_Table_Type_Table_Filter_Form_Edit,
                    },
                    {
                        href: WebPagePath.TableFilterFormEditView,
                        title: this.$i18n.Navigation_Table_Type_Table_Filter_Form_Edit_View,
                    },
                    {
                        href: WebPagePath.TableFormEditFormViewTable,
                        title: this.$i18n.Navigation_Table_Type_Table_Form_Edit_Form_View_Table,
                    },
                    {
                        href: WebPagePath.DownloadExcelImage,
                        title: this.$i18n.Navigation_Table_Type_Download_Excel_Image,
                    },
                    {
                        href: WebPagePath.Enroll,
                        title: this.$i18n.Navigation_Table_Type_Enroll,
                    },
                    {
                        href: WebPagePath.LogsDemo,
                        title: this.$i18n.Navigation_Table_Type_Logs,
                    },
                    {
                        href: WebPagePath.LicenseDemo,
                        title: this.$i18n.Navigation_Settings_License,
                    },
                ],
            },
        ];
    }
    //#endregion init nav

    //#region event
    private async onItemClick(event: Event, item: INavListBase, node): Promise<void> {
        const router: any = this.$router;
        const currentPath: string = router?.history?.current?.path;

        if (item.href === currentPath) {
            await this.$router.push(WebPagePath.Empty);
            await this.$router.push(item.href);
        }
    }
    //#endregion event
}
Vue.component('nav-src', NavSrc);
