






































import { Vue, Component } from 'vue-property-decorator';

import { ServiceI18n, StorageLanguage } from '@/../helpers';
import { IComponent } from '@/../models';

import * as Rx from 'rxjs';
import * as RxOperator from 'rxjs/operators';
import { setting$ } from '@/stores';

interface II18n extends IComponent.ISelectionBase {
    src: string;
}

@Component({
    components: {},
})
export default class Language extends Vue {
    //#region Variables
    private showI18n: boolean = false;
    private i18nSrc: string = require('@/../resources/images/en-US.png');
    private i18nData: II18n[] = [];

    private languageDefault: II18n = {
        objectId: 'en-US',
        name: 'English',
        src: require('@/../resources/images/en-US.png'),
    };
    private language: II18n = JSON.parse(JSON.stringify(this.languageDefault));
    private languageOptions: II18n[] = [];

    private nowLanguage: string = StorageLanguage.getStorage();

    private isNowLanguage: boolean = true;

    private stop$: Rx.Subject<{}> = new Rx.Subject();
    private showI18nKey: boolean = false;

    //#endregion Variables

    //#region Vue Life
    private beforeCreate(): void {}
    private created(): void {
        this.getIsI18nKey();
        this.initLanguage();
    }
    private beforeMount(): void {}
    private mounted(): void {}
    private beforeDestroy(): void {}
    private destroyed(): void {}
    //#endregion Vue Life

    //#region Funation
    private getIsI18nKey(): void {
        setting$.pipe(RxOperator.takeUntil(this.stop$)).subscribe({
            next: (x) => {
                this.showI18nKey = x.showI18nKey === true ?? false;
            },
        });
    }

    private clickI18n(): void {
        this.showI18n = !this.showI18n;
    }

    private initLanguage(): void {
        let self = this;
        let originalLanguage = JSON.parse(JSON.stringify(ServiceI18n.languageI18n));

        if (!this.showI18nKey) {
            delete originalLanguage['i18n-key'];
        }

        let storageLanguage = StorageLanguage.getStorage();
        this.languageOptions = [];

        // get language options
        let storageIsSupport: boolean = false;
        let originalLanguageKeys: string[] = Object.keys(originalLanguage);
        originalLanguageKeys.map(function (objectKey, index) {
            let tempData: II18n = {
                objectId: objectKey,
                name: originalLanguage[objectKey],
                src: require(`@/../resources/images/${objectKey}.png`),
            };

            self.languageOptions.push(tempData);

            if (storageLanguage && storageLanguage === tempData.objectId) {
                storageIsSupport = true;
                self.language = JSON.parse(JSON.stringify(tempData));
                self.i18nSrc = tempData.src;
            }

            // check whether storage language is still support; if not, need to set to default language
            if (index === originalLanguageKeys.length - 1 && storageIsSupport === false) {
                self.selectLanguage(originalLanguageKeys[0]);
            }
        });
    }

    private isSelectedI18n(selectedI18n: string): boolean {
        return this.nowLanguage === selectedI18n;
    }

    private selectLanguage(id: string): boolean {
        const selectedI18n: string = this.languageOptions.filter((item) => id === item.objectId)[0].objectId;

        if (this.nowLanguage === selectedI18n) return false;

        let router: any = this.$router;
        let tempdata: II18n = undefined;
        if (id) {
            tempdata = this.languageOptions.filter((item) => id === item.objectId)[0];
            this.language = tempdata;
            this.i18nSrc = tempdata.src;
            StorageLanguage.saveStorage(id);
        } else {
            this.language = JSON.parse(JSON.stringify(this.languageDefault));
            this.i18nSrc = require('@/../resources/images/en-US.png');
        }
        ServiceI18n.setVueI18n();
        if (router && router.history && router.history.current && router.history.current.fullPath) {
            let path = JSON.parse(JSON.stringify(router.history.current.fullPath));
            this.$router.go(path);
        }

        this.nowLanguage = StorageLanguage.getStorage();
    }

    //#endregion Funation
}
